<template>
  <div>
    <el-form :model="form" :rules="rules2" ref="form" label-position="left" label-width="0px" class="card-box loginform">
      <h3 class="title">{{systemName}}</h3>
      <el-form-item prop="username">
        <el-input type="text" v-model="form.username" auto-complete="off" placeholder=""></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input type="password" v-model="form.password" auto-complete="off" placeholder=""></el-input>
      </el-form-item>
      <el-form-item style="width:100%;">
        <el-button type="primary" style="width:100%;" :loading="loading" @click.native.prevent="handleSubmit2('form')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
    data() {
        return {
            title: '',
            systemName: 'postwoman',
            loading: false,
            form: {
                username: '',
                password: '',
            },
            rules2: {
                username: [{required: true, message: '', trigger: 'blur'}],
                password: [{required: true, message: '', trigger: 'blur'}],
            },
        }
    },
    methods: {
        handleSubmit2(form) {
            if (this.loading) return
            this.$refs.form.validate((valid) => {
                if (valid) {
                    this.loading = !this.loading
                    axios.post('auth/login', {
                        username: this.form.username,
                        password: this.form.password
                    }).then((res) => {
                        Cookies.set('access_token', res.data.access_token, { expires: 1 });
                        this.$message.success('');
                        this.$router.push('/');
                    }).catch((error) => {
                        this.loading = !this.loading
                        console.log(error.response);
                        this.$message.error(error.response.data.message);
                    })
                } else {
                    return false
                }
            })
        }
    }
}
</script>

<style>
.card-box {
	padding: 20px;
	/*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
	-webkit-border-radius: 5px;
	border-radius: 5px;
	-moz-border-radius: 5px;
	background-clip: padding-box;
	background-color: #F9FAFC;
	margin: 120px auto 20px;
	border: 2px solid #8492A6;
}

.title {
	margin: 0 auto 40px;
	text-align: center;
	color: #505458;
}
</style>


