<template>
    <div>
        <el-row :gutter="10">
            <el-col :span="4">
                <el-button icon="refresh" onclick="location.reload()"></el-button>
                <el-tabs>
                    <el-tab-pane label="">
                        <ul>
                            <li v-for="historyItem in historyList" style="width:100%;height:25px;line-height:25px;overflow: hidden;text-overflow: ellipsis;white-space: nowrap">
                                {{ historyItem.method.toUpperCase()}}  {{ historyItem.url }}
                            </li>
                        </ul>
                    </el-tab-pane>
                    <el-tab-pane label=""></el-tab-pane>
                </el-tabs>
            </el-col>
            <el-col :span="20">
                <el-tabs v-model="editableTabsValue" type="card" editable @edit="handleTabsEdit">
                    <el-tab-pane
                            :key="item.name"
                            v-for="(item, index) in editableTabs"
                            :label="item.content.url ? item.content.url : item.title"
                            :name="item.name"
                    >
                        <div class="request">
                            <el-input placeholder="Enter request URL" v-model="item.content.url" @keyup.enter="send(item.content)">
                                <el-select v-model="item.content.method" slot="prepend" placeholder="">
                                    <el-option label="GET" value="get"></el-option>
                                    <el-option label="POST" value="post"></el-option>
                                    <el-option label="PUT" value="put"></el-option>
                                    <el-option label="DELETE" value="delete"></el-option>
                                </el-select>
                                <el-button slot="append" @click="send(item.content)" :loading="loading"></el-button>
                            </el-input>
                            <el-tabs class="request-body">
                                <el-tab-pane label="Headers">
                                    <el-table :data="item.content.headers" style="width: 100%">
                                        <el-table-column label="Key">
                                            <template slot-scope="scope">
                                                <el-input v-model="scope.row.key"></el-input>
                                            </template>
                                        </el-table-column>
                                        <el-table-column label="Value">
                                            <template slot-scope="scope">
                                                <el-input v-model="scope.row.value"></el-input>
                                            </template>
                                        </el-table-column>
                                        <el-table-column label="Description">
                                            <template slot-scope="scope">
                                                <el-input v-model="scope.row.description"></el-input>
                                            </template>
                                        </el-table-column>
                                        <el-table-column label="" width="200">
                                            <template slot-scope="scope">
                                                <el-button type="success" icon="plus" @click="addHeader(item.content)"></el-button>
                                                <el-button type="danger" icon="minus" @click="removeHeader(item.content, scope.$index)"></el-button>
                                            </template>
                                        </el-table-column>
                                    </el-table>
                                </el-tab-pane>
                                <el-tab-pane label="Body" :disabled="item.content.method=='get'">
                                    <el-button type="primary" @click="item.content.dialogVisible = true"></el-button>
                                    <el-dialog
                                            title=""
                                            :visible.sync="item.content.dialogVisible"
                                            size="tiny">
                                        <el-input type="textarea" v-model="item.content.fromData" rows="10"></el-input>
                                        <el-button type="primary" class="mt10" @click="buildData(item.content)"></el-button>
                                    </el-dialog>
                                    <el-table :data="item.content.data" style="width: 100%;margin-top:10px;">
                                        <el-table-column label="Key">
                                            <template slot-scope="scope">
                                                <el-input v-model="scope.row.key"></el-input>
                                            </template>
                                        </el-table-column>
                                        <el-table-column label="Value">
                                            <template slot-scope="scope">
                                                <el-input v-model="scope.row.value"></el-input>
                                            </template>
                                        </el-table-column>
                                        <el-table-column label="Description">
                                            <template slot-scope="scope">
                                                <el-input v-model="scope.row.description"></el-input>
                                            </template>
                                        </el-table-column>
                                        <el-table-column label="" width="200">
                                            <template slot-scope="scope">
                                                <el-button type="success" icon="plus" @click="addData(item.content)"></el-button>
                                                <el-button type="danger" icon="minus" @click="removeData(item.content, scope.$index)"></el-button>
                                            </template>
                                        </el-table-column>
                                    </el-table>
                                </el-tab-pane>
                            </el-tabs>
                        </div>

                        <div class="response" v-loading="loading">
                            <el-tabs value="0" class="el-tabs">
                                <el-tab-pane label="Body">
                                        <div class="res-body" v-html="item.content.response.body" v-if="item.content.response.json"></div>
                                        <pre class="res-body" v-else>{{ item.content.response.body }}</pre>
                                </el-tab-pane>
                                <el-tab-pane label="Headers">
                                    <div class="res-header-list">
                                        <div class="res-header-item" v-for="(headerValue, headerKey) in item.content.response.headers">
                                            <b>{{ headerKey }}</b>  {{ headerValue }}
                                        </div>
                                    </div>
                                </el-tab-pane>
                            </el-tabs>
                        </div>
                    </el-tab-pane>
                </el-tabs>
            </el-col>
        </el-row>

    </div>
</template>

<script>
    import axios from 'axios';
    import jsonMarkup from 'json-markup';
    import 'json-markup/style.css'
export default {
    data() {
        return {
            editableTabsValue: '1',
            editableTabs: [{
                title: 'New Tab',
                name: '1',
                content:{
                    method:'get',
                    url:'',
                    fromData:'',
                    dialogVisible: false,
                    data:[
                        {key:'', value:'', description:''},
                    ],
                    headers:[
                        {key:'Content-Type', value:'application/x-www-form-urlencoded', description:''}
                    ],
                    response: {

                    }
                }
            }],
            tabIndex: 1,
            loading:false,
            historyList:[]
        }
    },
    created() {
        axios.get("http://localhost:3333/history")
            .then(response => {
                this.historyList = response.data;
            });
    },
    methods: {
        handleTabsEdit(targetName, action) {
            if (action === 'add') {
                let newTabName = ++this.tabIndex + '';
                this.editableTabs.push({
                    title: 'New Tab',
                    name: newTabName,
                    content:{
                        method:'get',
                        url:'',
                        data:[
                            {key:'', value:'', description:''}
                        ],
                        headers:[
                            {key:'', value:'', description:''}
                        ],
                        response: {

                        }
                    }
                });
                this.editableTabsValue = newTabName;
            }
            if (action === 'remove') {
                let tabs = this.editableTabs;
                let activeName = this.editableTabsValue;
                if (activeName === targetName) {
                    tabs.forEach((tab, index) => {
                        if (tab.name === targetName) {
                            let nextTab = tabs[index + 1] || tabs[index - 1];
                            if (nextTab) {
                                activeName = nextTab.name;
                            }
                        }
                    });
                }

                this.editableTabsValue = activeName;
                this.editableTabs = tabs.filter(tab => tab.name !== targetName);
            }
        },
        addHeader(content) {
            content.headers.push({key:'', value:'', description:''});
        },
        addData(content) {
            content.data.push({key:'', value:'', description:''});
        },
        removeHeader(content, key) {
            if (content.headers.length > 1) {
                content.headers.splice(key, 1);
            }
        },
        removeData(content, key) {
            if (content.data.length > 1) {
                content.data.splice(key, 1);
            }
        },
        buildData(content) {
            let fromData = content.fromData;
            let params = fromData.split('&');
            if (params.length>0) {
                let data = params.map(item => {
                    let param = item.split('=');
                    return {key:param[0], value:param[1], description:''};
                });
                content.data = data;
            } else {
                params = fromData.split("\n");
                console.log(params);
                if (params.length>0) {
                    let data = params.map(item => {
                        let param = item.split(':');
                        return {key:param[0], value:param[1], description:''};
                    });
                    content.data = data;
                }
            }
            content.dialogVisible = false;
        },
        send(content) {
            let url = content.url;
            if (!url) {
                this.$message.error('URL');
                return;
            }
            this.loading = !this.loading;
            let data = {};
            content.data.map(item => {
                if (!item.key) {
                    return;
                }
                data[item.key] = item.value;
            });
            let headers = {};
            content.headers.map(item => {
                if (!item.key) {
                    return;
                }
                headers[item.key] = item.value;
            });
            content.response = {};
            console.log(1);
            axios({
                method:'post',
                url:'http://localhost:3333/request',
                data:{
                    method:content.method,
                    url:url,
                    headers:headers,
                    data:data
                },
            }).then(response => {
                console.log(response);
                content.response = response.data;
                content.response.json = 0;
                try {
                    if (typeof content.response.body != 'object') {
                        content.response.body = JSON.parse(content.response.body);
                    }
                    content.response.body = jsonMarkup(content.response.body);
                    content.response.json = 1;
                } catch (e) {
                    console.log(e);
                }
                this.loading = !this.loading;
            }).catch(error => {
                this.loading = !this.loading;
            });
        }
    }
}
</script>

<style>
    ul,li{list-style: none;padding:0;margin:0;}
    .el-select .el-input {
        width: 110px;
    }
    .response {
        margin-top:30px;
        border-top:1px solid #ddd;
    }
    .request-body{
        max-height:500px;
        overflow: auto;
    }
    .res-body{
        padding:0 10px;
        height:500px;
        overflow:auto;
        word-break: break-all;
        word-wrap: break-word;
        border:2px solid #ddd;
        border-radius: 5px;
    }
    .res-header-list{padding:0 15px;}
    .res-header-item{margin-bottom:10px;}
</style>


